@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700&family=Work+Sans:wght@400;600&display=swap);
* {
    font-family: 'Work Sans', sans-serif;

    margin: 0;
    padding: 0;

    box-sizing: border-box;
}

:root {
    --white: #fff;
    --black: #222;
}

.main {
    display: flex;
    justify-content: center;
    align-items: center;
}
.movie__container {
    width: 100%;
    height: 100%;

    margin: 50px;

    display: grid;
    grid-template-columns: repeat(2, 550px);

    grid-column-gap: 20px;

    column-gap: 20px;
    grid-row-gap: 20px;
    row-gap: 20px;
}

.movie__grid {
    width: 100%;
    height: 100%;

    padding: 10px;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);

    grid-column-gap: 20px;

    column-gap: 20px;
}

.movie__image {
    width: 100%;
}

.movie__info__frame {
    display: grid;
    grid-template-rows: repeat(3, 1fr);
}

.movie__genres {
    font-size: 15px;

    list-style: none;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.movie__genres {
    margin-top: 10px;
}

@media (max-width: 966px) {
    .movie__container {
        grid-template-columns: repeat(1, 500px);
        grid-template-rows: repeat(2, 1fr);
    }
}

@media (max-width: 768px) {
    .movie__container {
        grid-template-columns: repeat(1, 300px);
        grid-template-rows: repeat(2, 1fr);
    }

    .movie__grid {
        grid-template-columns: repeat(1, 1fr);
    }

    .movie__info__frame {
        margin-top: 10px;
        grid-template-rows: repeat(3, 70px);
    }
}
.loading__frame {
    width: 100%;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
}

.loading__text {
    font-size: 30px;
    font-weight: 700;

    color: #333;
}
